* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  min-width: 80vw;
  margin-top: 30px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  margin: 0 auto 0 auto;
  width: 70%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.flex-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100%;
  height: 100%;
  background-color: rgb(43, 76, 226);
  padding-top: 20px;
  padding-bottom: 20px;
}

.movie-img {
  border-radius: 25px;
  width: 300px;
  height: 100%;
  margin: 10px auto;
}

.movie-overview {
  font-size: 1rem;
}

.movie-overview p {
  font-size: 1rem;
  display: flex;
  text-align: left;
  margin-top: 10px;
  margin-bottom: 10px;
  background: rgb(122, 91, 34);
  padding: 10px;
  border-radius: 10px;
  height: 100%;
}

.movie-title {
  font-size: 1.5rem;
  min-height: 100px;
  margin-top: 10px;
}

.movie-title-span {
  color: rgb(226, 144, 21);
}

.movie-date-span {
  color: rgb(187, 126, 216);
}

.logo {
  background-repeat: no-repeat;
  /* background: url(https://c.tenor.com/lIy9f2NtVZMAAAAC/count-down-movie-countdown.gif); */
  /* background: url('./images/5856b1184f6ae202fedf2789.png'); */
  background: url('./images/movies_films_reel_clapboard.jpg');
  border: none;
  padding: 10px 20px 35px 50px;
  background-size: 70px 45px;
  margin-left: 0px;
  border-radius: 5px;
}

nav {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  min-height: 5vh;
  background: rgb(110, 10, 1);
  color: aliceblue;
  width: 100%;
}

.nav-links {
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
  text-decoration: none;
}

.nav-links li a {
  margin: 10px 40px 10px 40px;
  text-decoration: none;
  color: #fff;
  font-weight: bold;
}

.nav-links a:hover {
  color: rgb(250, 241, 109);
  opacity: 0.7;
}

.nav-item {
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  position: fixed;
  /* Set the navbar to fixed position */
  top: 0;
  /* Position the navbar at the top of the page */
  width: 70%;
  /* Full width */
}

.overview-container {
  height: 250px;
}